<script>

export default {
  data: () => ({
    valid_thongtinchuhoso: false,
    citys: [],
    delegateDistricts: [],
    districts: [],
    delegateWards: [],
    wards: [],
    labelSwitch: {
      'true': {
        cmtnd: 'CMND/ H chiu',
        nguoi_nop: 'H v tn'
      },
      'false': {
        cmtnd: 'M s thu',
        nguoi_nop: 'Tn t chc/ c nhn'
      }
    }
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    },
    thongTinChuHoSo () {
      return this.$store.getters.thongTinChuHoSo
    },
    thongTinNguoiNopHoSo () {
      return this.$store.getters.thongTinNguoiNopHoSo
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      var filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 0,
        parent: 0
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.citys = result.data
      })
      setTimeout(function () {
        var thongTinChuHoSoTemp = vm.$store.getters.thongTinChuHoSo
        console.log('thongTinChuHoSoTemp------------', thongTinChuHoSoTemp)
        if (thongTinChuHoSoTemp.cityCode) {
          filter.parent = thongTinChuHoSoTemp.cityCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.districts = result.data
          })
        }
        if (thongTinChuHoSoTemp.districtCode) {
          filter.parent = thongTinChuHoSoTemp.districtCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.wards = result.data
          })
        }
        var thongTinNguoiNopHoSoTemp = vm.$store.getters.thongTinNguoiNopHoSo
        console.log('thongTinNguoiNopHoSoTemp------------', thongTinNguoiNopHoSoTemp)
        if (thongTinNguoiNopHoSoTemp.delegateCityCode) {
          filter.parent = thongTinNguoiNopHoSoTemp.delegateCityCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.delegateDistricts = result.data
          })
        }
        if (thongTinNguoiNopHoSoTemp.delegateDistrictCode) {
          filter.parent = thongTinNguoiNopHoSoTemp.delegateDistrictCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.delegateWards = result.data
          })
        }
      }, 200)
    })
  },
  watch: {
    thongTinChuHoSo: {
      handler: function () {
        var vm = this
        let value = vm.thongTinChuHoSo
        let tempData = {
          delegateName: value.applicantName,
          delegateCityCode: value.cityCode,
          delegateAddress: value.address,
          delegateDistrictCode: value.districtCode,
          delegateWardCode: value.wardCode,
          delegateEmail: value.contactEmail,
          delegateTelNo: value.contactTelNo,
          delegateIdNo: value.applicantIdNo
        }
        this.$store.dispatch('setThongTinNguoiNopHoSo', tempData)
      },
      deep: true
    },
    thongTinNguoiNopHoSo: {
      handler: function () {
        var vm = this
        let value = vm.thongTinNguoiNopHoSo
        if (value.sameUser) {
          this.$store.dispatch('setThongTinNguoiNopHoSo', vm.thongTinChuHoSo)
        } else {
          this.$store.dispatch('resetThongTinNguoiNopHoSo')
        }
      },
      deep: true
    }
  },
  methods: {
    onChangeCity (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.commit('setCityVal', data)
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.districts = result.data
        vm.wards = []
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.delegateDistricts = result.data
          vm.wards = []
        }
      })
    },
    onChangeDistrict (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.commit('setDistrictVal', data)
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.wards = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.delegateWards = result.data
        }
      })
    },
    onChangeWard (data) {
      this.$store.commit('setWardVal', data)
    },
    onChangeApplicantIdNo (data) {
      // this.$store.dispatch('getUserInfoFromApplicantIdNo', data)
    },
    onChangeDelegateCity (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.delegateDistricts = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.districts = result.data
        }
      })
    },
    onChangeDelegateDistrict (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.delegateWards = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.wards = result.data
        }
      })
    }
  }
}
</script>



